// 合作企业
import React, {Component} from 'react';
import {Col} from 'antd';
import '../styles.less'
import { getAction } from '../../../axios';

export default class CooperativeEnterprise extends Component {
  constructor(props) {
    super(props);
    this.state = {
      EnterpriseNum:'',
      personNum:'',
      classNum:'',
      bookNum:'',
      stuNum:'',
      EquipmentNum:''
    }
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
     if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
      this.getData(nextProps.currentLevel2Menu);
     }
  }
  getData(currentLevel2Menu){
    getAction('/bigdata/major/condition/info/queryMajorDonationInfo', {
      majorCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
        this.setState({
          EnterpriseNum:res.obj.EnterpriseNum||'',
          personNum:res.obj.personNum||'',
          classNum:res.obj.classNum||'',
          bookNum:res.obj.bookNum||'',
          stuNum:res.obj.stuNum||'',
          EquipmentNum:res.obj.EquipmentNum||''
        })
      }
    })
  }
  render(){
    return(
      <Col span={24} style={{
        padding:'10px 20px'
      }}>
        <div>
          <label className={'major_visual_teach_team_label'}>合作企业数量：</label>
          <span className={'major_visual_teach_team_text'}>{this.state.EnterpriseNum}个</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}>现代学徒制培养人数：</label>
          <span className={'major_visual_teach_team_text'}>{this.state.personNum}人</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}>联合开发课程数量：</label>
          <span className={'major_visual_teach_team_text'}>{this.state.classNum}门</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}>联合开发教材数量：</label>
          <span className={'major_visual_teach_team_text'}>{this.state.bookNum}本</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}>顶岗实习学生人数：</label>
          <span className={'major_visual_teach_team_text'}>{this.state.stuNum}</span>
        </div>
        <div>
          <label className={'major_visual_teach_team_label'}>捐赠设备总值：</label>
          <span className={'major_visual_teach_team_text'}>{this.state.EquipmentNum}万元</span>
        </div>
      </Col>
    )
  }
}
