// 教学改革
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import  '../styles.less'
import { getAction } from '../../../axios';
export default class TeachReform extends Component {
  constructor(props){
    super(props);
    this.state = {
      num:'',
      paper:'',
      book:''
    }
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
     if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
      this.getData(nextProps.currentLevel2Menu);
     }
  }
  getData(currentLevel2Menu){
    getAction('/bigdata/major/condition/info/queryTeachingReform', {
      majorCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
        this.setState({
          num:res.obj.num||'',
          paper:res.obj.paper||'',
          book:res.obj.book||'',
        })
      }
    })
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff",padding:'0 10%'}}>
        <Col span={24} className={'major_visual_teach_reform_number_large'}>{this.state.num}个</Col>
        <Col span={24} className={'major_visual_teach_reform_title'}>教学改革项目数量(院级以上)</Col>
        <Col span={12}>
          <div className={'major_visual_teach_reform_number_normal'}>{this.state.paper}个</div>
          <div className={'major_visual_teach_reform_title'} >获奖教研论文</div>
        </Col>
        <Col span={12}>
          <div className={'major_visual_teach_reform_number_normal'}>{this.state.book}个</div>
          <div className={'major_visual_teach_reform_title'}>获奖教材</div>
        </Col>
      </Row>
    )
  }
}
