// 基本情况
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import { getAction } from '../../../axios';
import '../styles.less'
export default class BasicInfo extends Component {
  constructor(props){
    super(props);
    this.state = {
      name:'',
      time:'',
      number:'',
      stuNum:'',
      situation:'',
      target:''
    }
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
     if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
      this.getData(nextProps.currentLevel2Menu);
     }
  }
  getData(currentLevel2Menu){
    getAction('/bigdata/major/condition/info/queryMajorBaseInfo', {
      majorCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
        this.setState({
          name:res.obj.name||'',
          time:res.obj.time||'',
          number:res.obj.number||'',
          stuNum:res.obj.stuNum||'',
          situation:res.obj.situation||'',
          target:res.obj.target||''
        })
      }
    })
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{paddingLeft:'10%'}}>
        <Col span={24} style={{margin:0}} className={'major_visual_basic_info_title'}>{this.state.name}</Col>
        <Col span={24} className={'major_visual_basic_info_content'}>
          <div><label>设置时间：</label><span>{this.state.time}</span></div>
          <div><label>专业代码：</label><span>{this.state.number}</span></div>
          <div><label>在校生人数：</label><span>{this.state.stuNum}</span></div>
          <div><label>建设现状：</label><span>{this.state.situation}</span></div>
          <div><label>建设目标：</label><span>{this.state.target}</span></div>
        </Col>
      </Row>
    )
  }
}
