import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import echarts from 'echarts';


class Barchart extends Component {
    state = {
        myChart:null,
        chartId:`FloorSpace${new Date().getTime()}-${Math.random()}`
    };

    componentDidMount() {
        let chartId=this.state.chartId
        let chart = echarts.init(document.getElementById(chartId));  
        this.setState({
            myChart:chart
        },()=>{
            this.setChart(this.props.data)
        })      
        
    }

    componentWillReceiveProps(nextProps) {
        if(JSON.stringify(this.props) !== (nextProps)){
            let chartId=this.state.chartId
            let chart = echarts.init(document.getElementById(chartId));  
            this.setState({
                myChart:chart
            },()=>{
                this.setChart(nextProps.data)
            })  
        }    
    }

    setChart = (data) => {
        if (!data || data.length <= 0) {
            return;
        }
        
        let xAxisData = data.map(item => item.name);    
        console.log(xAxisData)
        var option = {
            tooltip: {
                trigger: 'axis',
                axisPointer: { // 坐标轴指示器，坐标轴触发有效
                    type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            grid: {
                top:40,
                left: 20,
                right: 20,
                bottom: 20,
                containLabel: true
            },
            color: ['#2593fc', '#27c2c1', '#38c060', '#f9cb34', '#ed4a66', '#844add'],
            xAxis: {
                name: '时间',
                type: 'category',
                data:  data[0] && data[0]['value'] && data[0]['value'].map(item => {
                    return item.name;
                }),
                axisLabel:{
                    color: "#379fb7",
                }
            },
            yAxis: {
                name: '数量',
                type: 'value',
                axisLine:{
                    show:false,
                    lineStyle:{
                        color: "#379fb7",
                    }
                },
                axisLabel:{
                    color: "#379fb7",
                },
                splitLine:{
                    // show:false,
                    lineStyle: {
                        color: "#379fb7",
                        type: "dashed"
                    }
                }
            },
            series: data.map(item => {
                return {
                    name: item.name,
                    type: 'line',
                    barWidth: 30,
                    stack: '1',
                    data: item.value
                };
            })
        };

        this.state.myChart.setOption(option);
    };

    render() {

        return (
            <div id={this.state.chartId} style={{width: '100%', height: '100%'}}></div>
        );
    }
}

export default withRouter(Barchart);