import React, { Component } from 'react'
import { Carousel} from 'antd'
import { connect } from 'react-redux';
import BasicInfo from './components/BasicInfo'
import SelectionSituation from './components/SelectionSituation'
import TeachResource from './components/TeachResource'
import TeachReform from './components/TeachReform'
import TeachTeam from './components/TeachTeam'
import TrainingBase from './components/TrainingBase'
import CooperativeEnterprise from './components/CooperativeEnterprise'
import GraduateEmployment from './components/GraduateEmployment'
import StudentAward from './components/StudentAward'

// import Allmajor from './components/all'
// import Kuaiji from './components/kuaiji'
// import Jisuanji from './components/jisuanji'
// import Dianzi from './components/dianzi'
// import Yingxiao from './components/yingxiao'
// import Yunyong from './components/yunyong'

import BarchartComponent from './barchartComponent';
import jsonData from './jsonData';

class Zhuanye extends Component {
  render() {
    let autoplaySpeed = 40000;
        const settings = {
            // autoplay: true,
            autoplaySpeed,
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            beforeChange: function (currentSlide, nextSlide) {
                // console.log("before change", currentSlide, nextSlide);
            },
            afterChange: (currentSlide) => {
                if (currentSlide === 4) {
                    setTimeout(() => {
                        if (this.slider && this.slider.goTo) {
                            this.slider.goTo(0)
                        }
                    }, autoplaySpeed)
                }
            },
        };
    let { currentLevel2Menu } = this.props;
    return (
      <Carousel ref={slider => (this.slider = slider)} {...settings} >
        <div>     
        <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>     
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                基本情况
                            </span>

              </div>
              <div className='item-chart'>
              <BasicInfo currentLevel2Menu={currentLevel2Menu}></BasicInfo>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                招生情况
                            </span>

              </div>
              <div className='item-chart'>
              <SelectionSituation currentLevel2Menu={currentLevel2Menu}></SelectionSituation>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教学资源
                            </span>

              </div>
              <div className='item-chart'>
              <TeachResource currentLevel2Menu={currentLevel2Menu}></TeachResource>
              </div>              
            </div>
          </div>
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                教学改革
                            </span>

              </div>
              <div className='item-chart'>
              <TeachReform currentLevel2Menu={currentLevel2Menu}></TeachReform>
              </div>              
            </div>
          </div>    

          <div className='item-wrapper-4'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                师资队伍
                            </span>

              </div>
              <div className='item-chart'>
              <TeachTeam currentLevel2Menu={currentLevel2Menu}></TeachTeam>
              </div>              
            </div>
          </div>   
            
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                实训条件-实训基地
                            </span>

              </div>
              <div className='item-chart'>
              <TrainingBase currentLevel2Menu={currentLevel2Menu}></TrainingBase>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                培养效果-学生获奖情况
                            </span>

              </div>
              <div className='item-chart'>
              <StudentAward currentLevel2Menu={currentLevel2Menu}/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                培养效果-毕业就业情况
                            </span>

              </div>
              <div className='item-chart'>
              <GraduateEmployment currentLevel2Menu={currentLevel2Menu}/>
              </div>              
            </div>
          </div>   
          <div className='item-wrapper'>
            <div className='item-box'>
              <div className='item-title'>
                <span>
                校企合作-合作企业
                            </span>

              </div>
              <div className='item-chart'>
              <CooperativeEnterprise currentLevel2Menu={currentLevel2Menu}/>
              </div>              
            </div>
          </div>        

          {
            jsonData.map((item, i) => {
              if(item.type === 'bar'){
                return <BarchartComponent currentLevel2Menu={currentLevel2Menu} data={item} key={i}/>
              }else {
                return null;
              }
            })
          }
       
          {/* <Allmajor></Allmajor>
          <Kuaiji></Kuaiji>
          <Jisuanji></Jisuanji>
          <Dianzi></Dianzi>
          <Yingxiao></Yingxiao>
          <Yunyong></Yunyong> */}
        </div>
        
        </div>

      </Carousel>
    )
  }
}

export default connect((state)=> {
  return {
    currentLevel2Menu: state.level2MenuReducer.currentLevel2Menu
  }
})(Zhuanye)
