// 教学资源
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
import { getAction } from '../../../axios';
export default class TeachResource extends Component {
  constructor(props){
    super(props);
    this.state = {
      majorOrdinary:'',
      majorOne:'',
      boutiqueOrdinary:'',
      boutiqueOne:''
    }
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
     if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
      this.getData(nextProps.currentLevel2Menu);
     }
  }
  getData(currentLevel2Menu){
    getAction('/bigdata/major/condition/info/queryTeachingResouse', {
      majorCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
        this.setState({
          majorOrdinary:res.obj.majorOrdinary||'',
          majorOne:res.obj.majorOne||'',
          boutiqueOrdinary:res.obj.boutiqueOrdinary||'',
          boutiqueOne:res.obj.boutiqueOne||'',          
        })
      }
    })
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff",padding:'0 10%'}}>
        <Col span={24} className={'major_visual_teach_resource_title'}>专业教学资源库</Col>
        <Col span={6} style={{color:'rgba(0,207,255,1)'}}>普通</Col>
        <Col span={6} className={'major_visual_teach_resource_number'}>{this.state.majorOrdinary}个</Col>
        <Col span={6} style={{color:'rgba(0,207,255,1)'}}>一流</Col>
        <Col span={6} className={'major_visual_teach_resource_number'}>{this.state.majorOne}个</Col>
        <Col span={24} className={'major_visual_teach_resource_title'}>精品在线开放课程</Col>
        <Col span={6} style={{color:'rgba(0,207,255,1)'}}>普通</Col>
        <Col span={6} className={'major_visual_teach_resource_number'}>{this.state.boutiqueOrdinary}个</Col>
        <Col span={6} style={{color:'rgba(0,207,255,1)'}}>一流</Col>
        <Col span={6} className={'major_visual_teach_resource_number'}>{this.state.boutiqueOne}个</Col>
      </Row>
    )
  }
}
