import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import echarts from 'echarts';
import { getAction } from '../../axios';

let year = new Date().getFullYear();
let data = {};
for(let i = 3; i > 0; i--){
    data[(year - i) + '年'] = 0
}

class BarchartComponent extends Component {
    state = {
        myChart:null,
        chartId:`FloorSpace${new Date().getTime()}-${Math.random()}`
    };

    componentDidMount() {
        let chartId=this.state.chartId
        let chart = echarts.init(document.getElementById(chartId));  
        this.setState({
            myChart:chart
        },()=>{
            this.getData(this.props.currentLevel2Menu)
        })      
    }

    componentWillReceiveProps(nextProps) {
        if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
            let chartId=this.state.chartId
            let chart = echarts.init(document.getElementById(chartId));  
            this.setState({
                myChart:chart
            },()=>{
                this.getData(nextProps.currentLevel2Menu)
            })  
        }    
    }

    getData(currentLevel2Menu) {
        getAction('bigdata' + this.props.data.url, {
            majorCode: JSON.parse(currentLevel2Menu).code
        }).then((res) => {
            if (res.success && res.obj !== null) {
                this.setChart(res.obj[0])
            }else {
                this.setChart(data);
            }
        })
    }

    setChart = (barData) => {
        let data=[]
        for(let key in barData){
            data.push({
                name:key,
                value:barData[key]
            })
        }
        let xAxisData = data.map(item => item.name);
        var color = this.props.data.color || 0;

        var colors0 = ['#00CFFF', '#009CFF', '#0b6df4', '#252ff7'];
        var colors1 = ['#FA8C16', '#FA541C', '#c43707', '#dd1607'];

        var option = {
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b}: {c} "
            },
            grid: {
                left: 20,
                top: 30,
                bottom: 10,
                right: 20,
                containLabel: true
            },
            xAxis: {
                type: 'category',
                data: xAxisData,
                axisLine: {
                    lineStyle: {
                        color: "#00CFFF",
                        type: "dashed"
                    }
                },
                axisLabel: {
                    color: "#00CFFF",
                    interval: 0
                },
                axisTick: {
                    alignWithLabel: true
                }
            },
            yAxis: {
                type: 'value',
                axisLine: {
                    show: false
                },
                axisLabel: {
                    show: false
                },
                splitLine: {
                    show: false
                }
            },
            series: [
                {
                    name: '',
                    type: 'bar',
                    barMaxWidth: '20px',
                    itemStyle:{
                        normal: {
                            color: function(params) {
                                if(color === 0){
                                    return colors0[params.dataIndex];
                                }else {
                                    return colors1[params.dataIndex];
                                }
                            }
                        }
                    },
                    label: {
                        show: true,
                        position: "top",
                        color: "#fff",
                        fontSize: 14
                    },
                    data: data
                }
            ]
        };

        this.state.myChart.setOption(option);
    };

    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>{this.props.data.name}</span>
                    </div>
                    <div className='item-chart'>
                        <div id={this.state.chartId} style={{width: '100%', height: '100%'}}></div>
                    </div>
                </div>
            </div>
        );
    }
}

export default withRouter(BarchartComponent);