// 招生情况
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
import { getAction } from '../../../axios';
export default class SelectionSituation extends Component {
  constructor(props){
    super(props);
    this.state = {
      plan:'',
      number:'',
      firstRate:'',
      reportRate:''
    }
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
     if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
      this.getData(nextProps.currentLevel2Menu);
     }
  }
  getData(currentLevel2Menu){
    getAction('/bigdata/major/condition/info/queryMajorEnrollInfo', {
      majorCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null && JSON.stringify(res.obj) !== '{}'){
        this.setState({
          plan:res.obj.plan||'',
          number:res.obj.number||'',
          firstRate:res.obj.firstRate||'',
          reportRate:res.obj.reportRate||'',          
        })
      }
    })
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{height:"100%",textAlign:"center",color:"#fff"}}>
        <Col span={12}>
          <div className={'major_visual_selection_situation_num'}>{this.state.plan}</div>
          <div className={'major_visual_selection_situation_title'}>招生计划</div>
        </Col>
        <Col span={12}>
          <div className={'major_visual_selection_situation_num'}>{this.state.number}</div>
          <div className={'major_visual_selection_situation_title'}>录取人数</div>
        </Col>
        <Col span={12}>
          <div className={'major_visual_selection_situation_num'}>{this.state.firstRate}</div>
          <div className={'major_visual_selection_situation_title'}>第一志愿报考率</div>
        </Col>
        <Col span={12}>
          <div className={'major_visual_selection_situation_num'}>{this.state.reportRate}</div>
          <div className={'major_visual_selection_situation_title'}>报到率</div>
        </Col>
      </Row>
    )
  }
}
