// 师资队伍
import React, { Component } from 'react';
import { Col } from 'antd';
import  '../styles.less';
import BarEcharts from '../../../common/components/BaseEcharts/BarEcharts';
import PieEcharts from '../../../common/components/BaseEcharts/PieEcharts';
import echarts from 'echarts';
// import jsonData from '../../../data/major/专业视角--教师分类.json';
// import jsonData1 from '../../../data/major/专业视角--职称.json';
// import jsonData2 from '../../../data/major/专业视角--年龄.json';
import { getAction } from '../../../axios';
export default class TeachTeam extends Component {
  constructor(props) {
    super(props);
    this.state = {
      teaNum: '',
      majorLeader: '',
      awardNum: '',
      office: '',
      province: '',
      jsonData:{},
      jsonData1:[],
      jsonData2:[]
    };
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
    this.getData2(this.props.currentLevel2Menu);
    this.getData3(this.props.currentLevel2Menu);
    this.getData4(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
     if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
      this.getData(nextProps.currentLevel2Menu);
      this.getData2(nextProps.currentLevel2Menu);
      this.getData3(nextProps.currentLevel2Menu);
      this.getData4(nextProps.currentLevel2Menu);
     }
  }
  getData(currentLevel2Menu){
    getAction('/bigdata/major/condition/info/queryTeacherTeam1', {
      majorCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null){
        this.setState({
          teaNum:res.obj.teaNum||'',
          majorLeader:res.obj.majorLeader||'',
          awardNum:res.obj.awardNum||'',
          office:res.obj.office||'',
          province:res.obj.province||'',
        })
      }
    })
  }
  getData2(currentLevel2Menu){
    getAction('/bigdata/major/condition/info/queryTeacherTeam2', {
      majorCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null){
        this.setState({
         jsonData:res.obj
        })
      }
    })
  }
  getData3(currentLevel2Menu){
    getAction('/bigdata/major/condition/info/queryTeacherTeam3', {
      majorCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null){
        this.setState({
          jsonData1:res.obj
        })
      }
    })
  }
  getData4(currentLevel2Menu){
    getAction('/bigdata/major/condition/info/queryTeacherTeam4', {
      majorCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj){
        this.setState({
          jsonData2:res.obj
        })
      }
    })
  }
  render() {
    let all = 0;
    this.state.jsonData1.forEach((il) => {
      if(typeof il.value!=='number'){
        il.value = Number(il.value);
      }
      all = il.value+all;
    });
    // 柱状图
    let option = {
      title: {
        show: false,
      },
      xAxis: {
        data: this.state.jsonData.axis || [],
        axisLabel: {
          interval: 0,
          textStyle: {
            color: '#00CFFF',
          },
        },
      },
      yAxis: {
        axisLine: {
          show: false,
        },
        axisTick: {
          show: false,
        },
        axisLabel: {
          show: false,
        },
        splitLine: {
          // show:false,
          lineStyle: {
            color: '#379fb7',
            type: 'dashed',
          },
        },
      },
      series: [
        {
          type: 'bar',
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(
                0, 0, 0, 1,
                [
                  { offset: 0, color: 'rgba(0,207,255,1)' },
                  { offset: 0.5, color: 'rgba(0,207,255,0.8)' },
                  { offset: 1, color: 'rgba(0,207,255,0.19)' },
                ],
              ),
            },
          },
          label: {
            normal: {
              color: '#fff',
              show: true,
              position: 'top',
            },
          },
          data: this.state.jsonData.data||[],
        },
      ],
    };
    // 职称
    let pieOption = {
      title: {
        text: '职称',
        left: '23%',
        top: 'center',
        textStyle: {
          color: '#00CFFF',
          fontSize: '22',
          align: 'center',
        },
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)',
      },
      legend: {
        orient: 'vertical',
        x: '60%',
        y: 'center',
        icon: 'circle',
        itemWidth: 5,
        itemHeight: 5,
        itemGap: 13,
        textStyle: {
          color: '#00CFFF',
        },
        
      },
      color: ['#00CFFF', '#04FFC7', '#65D62D', '#F8E71C', '#FF2C2C'],
      series: [
        {
          name: '职称',
          type: 'pie',
          center: ['30%', '50%'],
          radius: ['50%', '70%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center',
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '30',
                fontWeight: 'bold',
              },
            },
          },
          labelLine: {
            normal: {
              show: false,
            },
          },
          data: this.state.jsonData1,
        },
      ],
    };
    // 年龄
    let pieOption1 = {
      title: {
        text: '年龄',
        left: '23%',
        top: 'center',
        textStyle: {
          color: '#00CFFF',
          fontSize: '22',
          align: 'center',
        },
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)',
      },
      legend: {
        orient: 'vertical',
        x: '60%',
        y: 'center',
        icon: 'circle',
        itemWidth: 5,
        itemHeight: 5,
        itemGap: 13,
        textStyle: {
          color: '#00CFFF',
        },
        
      },
      color: ['#00CFFF', '#13C2C2', '#2FC25B', '#FACC14', '#F04864'],
      series: [
        {
          name: '年龄',
          type: 'pie',
          center: ['30%', '50%'],
          radius: ['50%', '70%'],
          avoidLabelOverlap: false,
          label: {
            color: '#00CFFF',
            normal: {
              show: false,
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '30',
                fontWeight: 'bold',
              },
            },
          },
          labelLine: {
            normal: {
              show: false,
            },
          },
          data: this.state.jsonData2,
        },
      ],
    };
    return (
      <Col span={24}  style={{height:'100%'}}>
        <Col span={6} style={{
          padding: '20px 20px'
        }}>
          <div>
            <label className={'major_visual_teach_team_label'}>本专业教师数量：</label>
            <span className={'major_visual_teach_team_text'}>{this.state.teaNum}人</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}>专业带头人：</label>
            <span className={'major_visual_teach_team_text'}>{this.state.majorLeader}</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}>教师获奖总数：</label>
            <span className={'major_visual_teach_team_text'}>{this.state.awardNum}人</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}>厅级奖项：</label>
            <span className={'major_visual_teach_team_text'}>{this.state.office}人</span>
          </div>
          <div>
            <label className={'major_visual_teach_team_label'}>省部级奖项：</label>
            <span className={'major_visual_teach_team_text'}>{this.state.province}人</span>
          </div>
        </Col>
        <Col span={6} style={{height:'100%'}}>
          <BarEcharts
            id='TeachTeamBar'
            data={option}
            height='100%'/>
        </Col>
        <Col span={6} style={{height:'100%'}}>
          <PieEcharts
            id='TeachTeamPie1'
            data={pieOption}
            height='100%'/>
        </Col>
        <Col span={6} style={{height:'100%'}}>
          <PieEcharts
            id='TeachTeamPie2'
            data={pieOption1}
            height='100%'/>
        </Col>
      </Col>
    );
  }
}
