// 学生获奖情况
import React, {Component} from 'react';
import {Col} from 'antd';
import Duidie from './duidie'
import { getAction } from '../../../axios';
export default class StudentAward extends Component {
  state={
    data:[]
  }
  componentDidMount(){
    this.getData(this.props.currentLevel2Menu);
  }
  componentWillReceiveProps(nextProps){
     if(JSON.stringify(this.props) !== JSON.stringify(nextProps)){
      this.getData(nextProps.currentLevel2Menu);
     }
  }
  getData(currentLevel2Menu){
    getAction('/bigdata/major/condition/info/queryMajorStudentAward', {
      majorCode: JSON.parse(currentLevel2Menu).code
    }).then(res => {
      if(res.success && res.obj !== null){
        this.setState({
          data: res.obj
        })
      }
    })
  }
  render(){
    return(
      <Col span={24} style={{height:'100%'}}>
        <Duidie data={this.state.data}/>
      </Col>
    )
  }
}
